}

input:focus + i{
  color: white;
  }
input::-webkit-input-placeholder{
    color: white;
    opacity:.6;
  }
input::placeholder{
    color: white;
    opacity:.6;
  }
i{
  color: gray;
  position: absolute;
  left: 6px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  transition: color .3s;
}
  
.AppLogin button{
  background:  linear-gradient(to top right, #302602 -5%, #b67e03 100%);
  border: none;
  border-radius: 30px; 
  color: lightgray;
  font-size: 16px;
